import * as yup from 'yup';

export const countries = [
	'–£–∫—Ä–∞–∏–Ω–∞',
	'–ù–∏–∫–∞—Ä–∞–≥—É–∞',
	'–°–®–ê',
	'–í–µ–ª–∏–∫–æ–±—Ä–∏—Ç–∞–Ω–∏—è',
	'–ì—Ä—É–∑–∏—è',
];

export const cities = [
	'–ö–∏–µ–≤',
	'–õ—å–≤–æ–≤',
	'–û–¥–µ—Å—Å–∞',
	'–ù—å—é-–ô–æ—Ä–∫',
	'–ú–µ–ª–∏—Ç–æ–ø–æ–ª—å',
	'–•–∞—Ä—å–∫–æ–≤',
];

export const genders = ['–ú—É–∂—Å–∫–æ–π', '–ñ–µ–Ω—Å–∫–∏–π'];

export const languages = [
	'–£–∫—Ä–∞–∏–Ω—Å–∫–∏–π',
	'–†—É—Å—Å–∫–∏–π',
	'–ê–Ω–≥–ª–∏–π—Å–∫–∏–π',
	'–ò—Å–ø–∞–Ω—Å–∫–∏–π',
	'–ù–µ–º–µ—Ü–∫–∏–π',
	'–ë–æ–ª–≥–∞—Ä—Å–∫–∏–π',
];

export const hobbies = [
	'–ß—Ç–µ–Ω–∏–µ',
	'–ü—É—Ç–µ—à–µ—Å—Ç–≤–∏—è',
	'–ü—Ä–æ–≥—Ä–∞–º–º–∏—Ä–æ–≤–∞–Ω–∏–µ',
	'–°–ø–æ—Ä—Ç',
	'–•–æ–±–∏—Ö–æ—Ä—Å–∏–Ω–≥',
	'–ö–≤–∞–¥—Ä–æ–±–±–∏–Ω–≥',
];

export const schema = {
	firstName: yup
		.string()
		.matches(/^[A-Za-z–ê-–Ø–∞-—è–Å—ë–Ü—ñ–á—ó–Ñ—î]+$/, '–¢–æ–ª—å–∫–æ –±—É–∫–≤—ã')
		.min(2, '–ú–∏–Ω–∏–º—É–º 2 —Å–∏–º–≤–æ–ª–∞')
		.max(30, '–ú–∞–∫—Å–∏–º—É–º 30 —Å–∏–º–≤–æ–ª–æ–≤')
		.required('–ò–º—è –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ'),

	lastName: yup
		.string()
		.matches(/^[A-Za-z–ê-–Ø–∞-—è–Å—ë–Ü—ñ–á—ó–Ñ—î]+$/, '–¢–æ–ª—å–∫–æ –±—É–∫–≤—ã')
		.min(2, '–ú–∏–Ω–∏–º—É–º 2 —Å–∏–º–≤–æ–ª–∞')
		.max(30, '–ú–∞–∫—Å–∏–º—É–º 30 —Å–∏–º–≤–æ–ª–æ–≤')
		.required('–§–∞–º–∏–ª–∏—è –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–∞'),

	phone: yup
		.string()
		.matches(/^\+?[1-9]\d{9,15}$/, '–ù–µ–∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π –Ω–æ–º–µ—Ä')
		.required('–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ –æ–±—è–∑–∞—Ç–µ–ª–µ–Ω'),

	nickname: yup
		.string()
		.matches(/^[A-Za-z0-9_]+$/, '–¢–æ–ª—å–∫–æ –ª–∞—Ç–∏–Ω–∏—Ü–∞, —Ü–∏—Ñ—Ä—ã –∏ _')
		.min(3, '–ú–∏–Ω–∏–º—É–º 3 —Å–∏–º–≤–æ–ª–∞')
		.max(20, '–ú–∞–∫—Å–∏–º—É–º 20 —Å–∏–º–≤–æ–ª–æ–≤')
		.required('–ù–∏–∫–Ω–µ–π–º –æ–±—è–∑–∞—Ç–µ–ª–µ–Ω'),

	email: yup.string().email('–ù–µ–∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π email').required('Email –æ–±—è–∑–∞—Ç–µ–ª–µ–Ω'),

	password: yup
		.string()
		.min(8, '–ú–∏–Ω–∏–º—É–º 8 —Å–∏–º–≤–æ–ª–æ–≤')
		.max(50, '–ú–∞–∫—Å–∏–º—É–º 50 —Å–∏–º–≤–æ–ª–æ–≤')
		.matches(/[A-Z]/, '–ü–∞—Ä–æ–ª—å –¥–æ–ª–∂–µ–Ω —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Ö–æ—Ç—è –±—ã –æ–¥–Ω—É –∑–∞–≥–ª–∞–≤–Ω—É—é –±—É–∫–≤—É')
		.matches(/[a-z]/, '–ü–∞—Ä–æ–ª—å –¥–æ–ª–∂–µ–Ω —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Ö–æ—Ç—è –±—ã –æ–¥–Ω—É —Å—Ç—Ä–æ—á–Ω—É—é –±—É–∫–≤—É')
		.matches(/\d/, '–ü–∞—Ä–æ–ª—å –¥–æ–ª–∂–µ–Ω —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Ö–æ—Ç—è –±—ã –æ–¥–Ω—É —Ü–∏—Ñ—Ä—É')
		.matches(
			/[@$!%*?&]/,
			'–ü–∞—Ä–æ–ª—å –¥–æ–ª–∂–µ–Ω —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Ö–æ—Ç—è –±—ã –æ–¥–∏–Ω —Å–ø–µ—Ü–∏–∞–ª—å–Ω—ã–π —Å–∏–º–≤–æ–ª (@, $, !, %, *, ?, &)'
		)
		.required('–ü–∞—Ä–æ–ª—å –æ–±—è–∑–∞—Ç–µ–ª–µ–Ω'),

	country: yup.string().required('–°—Ç—Ä–∞–Ω–∞ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–∞'),

	city: yup.string().required('–ì–æ—Ä–æ–¥ –æ–±—è–∑–∞—Ç–µ–ª–µ–Ω'),

	gender: yup.string().required('–ü–æ–ª –æ–±—è–∑–∞—Ç–µ–ª–µ–Ω'),

	language: yup.string().required('–Ø–∑—ã–∫ –æ–±—è–∑–∞—Ç–µ–ª–µ–Ω'),

	hobby: yup
		.array()
		.min(1, '–í—ã–±–µ—Ä–∏—Ç–µ —Ö–æ—Ç—è –±—ã –æ–¥–Ω–æ —Ö–æ–±–±–∏')
		.required('–•–æ–±–±–∏ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ'),
};
